.btn-donation {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  font-size: 1.1rem;
  font-weight: 700;
  text-transform: uppercase;
  white-space: nowrap;
  color: var(--color-white);
  letter-spacing: 1px;
  padding: 1.5rem;
  background-color: var(--color-button-donat);
  border-radius: .5rem;
  transition: all .3s ease 0s;
  z-index: 3;
}

button {
  box-shadow: 3px 3px 50px -15px var(--color-accent);
}

@media (max-width: 490px) {
  .menu__btn {
    position: absolute;
    top: 7rem;
    left: -100%;
    transition: all .3s ease 0s;
  }

  .menu__btn._active {
    left: 1rem;
    transition: all .3s ease 0s;
  }
}
